import { useState } from 'react';
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme, styled } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import IconButton from '@mui/material/IconButton';
import PhotoCamera from '@mui/icons-material/PhotoCamera';
import Stack from '@mui/material/Stack';

const Input = styled('input')({
  display: 'none'
});
function UpluadImage({ onClose, onUpload, open }) {
  const [image, setImage] = useState(null);
  const [caption, setCaption] = useState('');
  const [isPrivate, setIsPrivate] = useState(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

  const handleClose = () => {
    onClose();
  };

  const handleUpload = () => {
    onUpload(image, caption, isPrivate);
  };

  return (
    <Dialog
      fullScreen={fullScreen}
      open={open}
      onClose={handleClose}
      aria-labelledby="responsive-dialog-title"
    >
      <DialogTitle id="responsive-dialog-title">העלאת תמונה</DialogTitle>
      <DialogContent>
        <DialogContentText>צירוץ תמונה לפוסט החדש</DialogContentText>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <input
              accept="image/*"
              type="file"
              onChange={(e) => {
                setImage(e.target.files[0]);
              }}
            />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary">
          ביטול
        </Button>
        <Button disabled={!image} onClick={handleUpload} color="primary" autoFocus>
          שמירה
        </Button>
      </DialogActions>
    </Dialog>
  );
}

function NewPostForm() {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const [postData, setPostData] = useState({
    title: '',
    content: '',
    image: null,
    caption: '',
    isPrivate: false
  });
  const handleUpload = (image, caption, isPrivate) => {
    setPostData({
      ...postData,
      image,
      caption,
      isPrivate
    });
    handleClose();
  };
  return (
    <>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <TextField
            required
            id="postTitle"
            name="postTitle"
            label="כותרת הפוסט"
            fullWidth
            variant="standard"
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            required
            multiline
            rows={10}
            id="postContent"
            name="postContent"
            label="תוכן הפוסט"
            fullWidth
            variant="standard"
          />
        </Grid>
        <Grid item xs={12}>
          <IconButton
            text="Upload Image"
            variant="contained"
            color="primary"
            onClick={handleOpen}
            style={{ marginTop: '10px' }}
            aria-label="upload picture"
            component="span"
          >
            <PhotoCamera />
          </IconButton>
          <UpluadImage open={open} onClose={handleClose} onUpload={handleUpload} />
        </Grid>
      </Grid>
    </>
  );
}
// -------------------------------
export default function NewPostDialog({ open, handleClose }) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'));
  return (
    <Dialog onClose={handleClose} open={open} fullScreen={fullScreen}>
      <DialogTitle>יצירת פוסט חדש</DialogTitle>
      <DialogContent>
        <DialogContentText>
          <NewPostForm />
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button autoFocus onClick={handleClose}>
          ביטול
        </Button>
        <Button onClick={handleClose} autoFocus>
          פרסום
        </Button>
      </DialogActions>
    </Dialog>
  );
}
NewPostDialog.propTypes = {
  open: PropTypes.bool.isRequired
};
