import React, { useEffect } from 'react';
// material
import { Box, Grid, Container, Typography } from '@mui/material';
import PeopleIcon from '@mui/icons-material/People';
import SnippetFolderIcon from '@mui/icons-material/SnippetFolder';
import CreateNewFolderIcon from '@mui/icons-material/CreateNewFolder';
import NumberCounter from 'number-counter';
// components
import Page from '../components/Page';
import {
  AppTasks,
  AppProjectsLength,
  AppTasksLength,
  AppNewsUpdate,
  AppUsersLength,
  AppOrderTimeline,
  AppCurrentVisits,
  AppWebsiteVisits,
  AppTrafficBySite,
  AppCurrentSubject,
  AppConversionRates
} from '../components/_dashboard/app';
import { useAuthContext } from '../context/UserProvider';
import useLocalStorge from '../hooks/useLocalStorge';
// ----------------------------------------------------------------------

export default function DashboardApp() {
  const { dashboardPublicData } = useAuthContext();
  const [dashboardData, setDashboardData] = useLocalStorge('dashboardData', {});

  useEffect(() => {
    dashboardPublicData()
      .then((res) => setDashboardData(res))
      .catch((err) => console.log(err));
  }, []);

  return (
    <Page title="#כולא-לייק | לוח ניהול">
      <Container maxWidth="xl">
        <Box sx={{ pb: 5 }}>
          <Typography variant="h4">כולא-לייק</Typography>
        </Box>
        <Grid container spacing={3}>
          {dashboardData && (
            <>
              <Grid item xs={12} sm={6} md={4}>
                <AppUsersLength
                  number={<NumberCounter end={dashboardData.usersLength} delay={1} />}
                  string="משתמשים במערכת"
                  icon={<PeopleIcon />}
                />
              </Grid>
              <Grid item xs={12} sm={6} md={4}>
                <AppProjectsLength
                  number={<NumberCounter end={dashboardData.projectsLength} delay={1} />}
                  string="פרוייקטים פעילים"
                  icon={<SnippetFolderIcon />}
                />
              </Grid>
              <Grid item xs={12} sm={6} md={4}>
                <AppTasksLength
                  number={<NumberCounter end={dashboardData.tasksLength} delay={1.2} />}
                  string="מספר משימות"
                  icon={<CreateNewFolderIcon />}
                />
              </Grid>
            </>
          )}

          <Grid item xs={12} md={6} lg={8}>
            <AppWebsiteVisits />
          </Grid>

          <Grid item xs={12} md={6} lg={4}>
            <AppCurrentVisits />
          </Grid>

          <Grid item xs={12} md={6} lg={8}>
            <AppConversionRates />
          </Grid>

          <Grid item xs={12} md={6} lg={4}>
            <AppCurrentSubject />
          </Grid>

          <Grid item xs={12} md={6} lg={8}>
            <AppNewsUpdate />
          </Grid>

          <Grid item xs={12} md={6} lg={4}>
            <AppOrderTimeline />
          </Grid>

          <Grid item xs={12} md={6} lg={4}>
            <AppTrafficBySite />
          </Grid>

          <Grid item xs={12} md={6} lg={8}>
            <AppTasks />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
}
