import React, { useState } from 'react';
import { Icon } from '@iconify/react';
import plusFill from '@iconify/icons-eva/plus-fill';
import { Link as RouterLink } from 'react-router-dom';
// material
import { Grid, Button, Container, Stack, Typography } from '@mui/material';
// components
//
import { OpenInNewTwoTone } from '@mui/icons-material';
import POSTS from '../_mocks_/blog';
import NewPostDialog from './NewPost';
import Page from '../components/Page';
import { BlogPostCard, BlogPostsSort, BlogPostsSearch } from '../components/_dashboard/blog';

// ----------------------------------------------------------------------

const SORT_OPTIONS = [
  { value: 'חדשים', label: 'חדשים' },
  { value: 'פופולריים', label: 'פופולריים' },
  { value: 'ישנים', label: 'ישנים' }
];
// ----------------------------------------------------------------------

export default function Blog() {
  const [open, setOpen] = useState(false);
  const hendleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  return (
    <Page title="#כולא-לייק | לוח ניהול: עדכונים">
      <Container>
        <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
          <Typography variant="h4" gutterBottom>
            עדכונים וחדשות
          </Typography>
          <Button
            variant="contained"
            onClick={hendleOpen}
            endIcon={<Icon icon={plusFill} style={{ marginRight: '.5rem' }} />}
          >
            יצירת פוסט חדש
          </Button>
        </Stack>
        <NewPostDialog open={open} handleClose={handleClose} />
        <Stack mb={5} direction="row" alignItems="center" justifyContent="space-between">
          <BlogPostsSearch posts={POSTS} />
          <BlogPostsSort options={SORT_OPTIONS} />
        </Stack>

        <Grid container spacing={3}>
          {POSTS.map((post, index) => (
            <BlogPostCard key={post.id} post={post} index={index} />
          ))}
        </Grid>
      </Container>
    </Page>
  );
}
